@use 'base/variables' as v;
@use 'base/mixins' as m;

.horas {
    @include m.reset-lista;
    @include m.grid(1, 1rem);
    @include m.tablet {
        @include m.grid(2, 1rem)
    }
    &__hora {
        color: v.$azul;
        border: 1px solid v.$azul;
        border-radius: .5rem;
        padding: 1rem;
        text-align: center;
        transition: background-color, color;
        transition-duration: 300ms;
        &:hover {
            cursor: pointer;
            background-color: v.$azul;
            color: v.$blanco;
        }
        &--seleccionada {
            background-color: v.$verde;
            color: v.$blanco;
            border: none;
            &:hover{
                cursor: pointer;
                background-color: v.$verde;
            }
        }
        &--selected-or {
            &:hover {
                background-color: v.$naranja;
            }
        }
        &--deshabilitada {
            border: 1px solid v.$gris-oscuro;
            color: v.$gris-oscuro;
            opacity: .5;
            &:hover {
                background-color: v.$blanco;
                color: v.$gris-oscuro;
                cursor: not-allowed;
            }
        }
    }
}