@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion {
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @include m.tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: v.$azul;
        font-size: 1.6rem;
        &--texto {
            padding: .5rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;
            &:hover {
                background-color: v.$azul;
                color: v.$blanco;
            }
        }
        &--numero {
            @extend .paginacion__enlace--texto;
        }
        &--actual {
            color: v.$gris-oscuro;
            font-weight: v.$bold;
        }
    }
}