@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos {

    &__grid {
        @include m.contenedor-sm;
        @include m.grid(1, 5rem)
    }
}

.boleto {
    width: min(100% - 5rem, 65rem);
    margin: 0 auto;
    padding: 6rem;
    border-radius: 2rem;
    color: v.$blanco;
    position: relative;

    &::before,
    &::after {
        position: absolute;
        content: '';
        background-color: v.$blanco;
        width: 5rem;
        height: 5rem;
        top: calc(50% - 2.5rem);
    }

    &::before {
        clip-path: circle(50% at 15% 50%);
        left: 0;
    }

    &::after {
        clip-path: circle(50% at 85% 50%);
        right: 0;
    }

    &--presencial {
        background-color:hsla(132,100%,50%,1);
        background-image:
        radial-gradient(at 40% 20%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 94% 18%, hsla(201,100%,56%,1) 0px, transparent 50%),
        radial-gradient(at 20% 45%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 90% 100%, hsla(289,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 7% 86%, hsla(189,100%,56%,1) 0px, transparent 50%),
        radial-gradient(at 16% 64%, hsla(194,0%,0%,1) 0px, transparent 50%),
        radial-gradient(at 18% 16%, hsla(237,100%,51%,1) 0px, transparent 50%);
    }
    &--virtual {
        background-color: v.$secundario;
    }
    &--gratis {
        background-color: v.$azul;
    }

    &__logo {
        @include m.logo;
        @include m.texto-fluido(2rem, 4rem);
    }
    &__plan {
        font-size: 2rem;
        text-transform: uppercase;
        margin:3rem 0 0 0;
    }
    &__precio {
        margin: 3rem 0 0 0;
        font-weight: v.$black;
        font-size: 4rem;
    }

    &__enlace-contenedor {
        margin-top: 5rem;
        display: flex;
        justify-content: center;
    }

    &__enlace {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
    }
    $modificador: &;
    &--acceso {
        width: 30rem;
        height: 50rem;
        padding: 10rem 3rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @include m.tablet {
            width: 60rem;
            height: 30rem;
            padding: 5rem;
        }

        #{$modificador}__logo {
            @include m.texto-fluido(2rem, 3rem);
            margin: 0;
            text-align: left;
        }
        #{$modificador}__nombre {
            font-size: 2.4rem;
            text-transform: uppercase;
            font-weight: v.$black;
        }
        #{$modificador}__codigo {
            font-size: 4rem;
            font-weight: v.$bold;
            text-align: center;
            position: absolute;
            bottom: 2rem;
            left: 0;
            width: 100%;
            @include m.tablet {
                rotate: 90deg;
                transform-origin: 0 0;
                top: -35px;
                left: calc(100% - 5rem);
                width: 30rem;
            }
        }
    }
    &::before{
        clip-path: circle(50% at 50% 15%);
        left: calc(50% - 2.5rem);
        top: 0;
        @include m.tablet {
            clip-path: circle(50% at 15% 50%);
            left: 0;
            top: calc(50% - 2.5rem);
        }
    }
    &::after {
        clip-path: circle(50% at 50% 85%);
        left: calc(50% - 2.5rem);
        top: calc(100% - 5rem);
        @include m.tablet {
            clip-path: circle(50% at 85% 50%);
            left: calc(100% - 5rem);
            top: calc(50% - 2.5rem);
        }
    }
}